let exchangeSell = $("input[name='sell']")
let exchangeBuy = $("input[name='buy']")


setCheckedExchange('sell')
setCheckedExchange('buy')



$(exchangeSell).change(function () {
    setCheckedExchange('sell')
})

$(exchangeBuy).change(function () {
    setCheckedExchange('buy')
})


function setCheckedExchange(type){
    closeExchangeSelect()
    if(type === 'sell'){
        let checked = document.querySelector("input[name='sell']:checked")
        setAllExchangeProps(type,checked.dataset)

        $(exchangeBuy).each(function (i,input) {
            $(input).prop("disabled",false)
        })
        document.querySelector(`input[name='buy'][data-currency='${checked.dataset.currency}']`).setAttribute('disabled','')
        document.querySelector(`input[name='buy'][data-currency='${checked.dataset.currency}']`).checked = false;
    }
    if(type === 'buy'){
        let checked = document.querySelector("input[name='buy']:checked")
        setAllExchangeProps(type,checked.dataset)
    }
}


function setAllExchangeProps(type,data) {
    let exchangeIcon = $(`.exchange[data-exchange-type='${type}'] .exchange__input-icon img`)
    let currencies = $(`.exchange[data-exchange-type='${type}'] .currency-item`)
    let min = $(`.exchange[data-exchange-type='${type}'] .currency-min`)
    let max = $(`.exchange[data-exchange-type='${type}'] .currency-max`)
    let currentCurrency = $(`input[name='${type}']:checked + label span`).html()

    $(exchangeIcon).attr("src",data.imgSrc)
    $(currencies).html(data.currency)
    $(min).html(data.min)
    $(max).html(data.max)
    $(`.exchange[data-exchange-type='${type}'] .exchange__select input`).val(currentCurrency)
}



$(".exchange__select").click(function () {
    $(this).parents(".exchange").addClass("_active")
})


function closeExchangeSelect() {
    $(".exchange").removeClass("_active")
}


$(document).mouseup(function(e) {
    var div = $('.exchange__items');
    if (!div.is(e.target) && div.has(e.target).length === 0) {
        if($(div).parents(".exchange").hasClass("_active")){
            $(div).parents(".exchange").removeClass("_active")
        }
    }
});
